import React from 'react'
import {withRouter} from 'react-router-dom'
import PropTypes from 'prop-types'
import Icon from 'components/Icon-svg'
import './index.less'
@withRouter
export default class extends React.Component {
    static propTypes = {
        title: PropTypes.string,
        backgroundColor: PropTypes.string
    }
    static defaultProps = {
        title: '戏库',
        backgroundColor: '#fff'
    }
    render () {
        return (
            <section className="title-bar-wrapper" style={{backgroundColor: this.props.backgroundColor}}>
                <div className="head">
                    <img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>
                    <h2>{this.props.title}</h2>
                </div>
            </section>
        )
    }
}



